import { Component, OnInit, NgZone, ElementRef, AfterViewInit } from '@angular/core';
import { createAnimation } from '@ionic/core';
import { Room, Reservation } from 'src/model/room';
import { RoomReservationService } from './room-reservation.service';
import { FormArray, FormBuilder, ReactiveFormsModule, FormGroup, Validators } from '@angular/forms';
import { AlertController, NavController } from '@ionic/angular';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { NavigationEnd, NavigationExtras, Router } from '@angular/router';
import { Store, select } from '@ngrx/store';
import { Notification } from 'src/app/store/models/notification';
import { TodoAdd } from '../../../store//actions/notification-action';
import { GeneralFunctions } from '../../general-functions';


@Component({
  selector: 'app-room-reservation',
  templateUrl: './room-reservation.page.html',
  styleUrls: ['./room-reservation.page.scss'],

})
export class RoomReservationPage implements OnInit {

  inputStart: boolean;
  inputEnd: boolean;
  rooms: any[] = [];
  isBottomSheetOpened = false;
  localstorageReservations: Reservation[] = [];
  registerForm: any;
  bookingForm: FormGroup;
  reservation;
  currentRoom!: Room | null;
  roomName: string;
  roomHistorical = [];
  multiplicateReservation: boolean;
  wrongReservation: boolean;
  convertedDate;
  localISOTime;
  currentDate;
  showMap = false;
  showReservationsContent = true;
  notification: Observable<any[]>;

  constructor(
    private roomReservationService: RoomReservationService,
    private fb: FormBuilder,
    public alertController: AlertController,
    public router: Router,
    private navigateController: NavController,
    private store: Store<{ notification: Notification[] }>,
    public generalFunctions: GeneralFunctions

  ) { }

  public info = {title: 'Réservation de', subTitle: 'salle'};

  initializeForm(): void {
    this.bookingForm = this.fb.group({
      dateStart: '',
      dateEnd: '',
      references: this.fb.array([this.fb.control('')])
    });
  }

  ngAfterViewInit() {
    this.rooms = this.roomReservationService.getrooms();
    this.generalFunctions.gestionDeReservation(this.rooms, 'reservations', 'historicalReservations');
  }

  ngOnInit() {
    let salles = [{"id":"Accueil E1","color":"#31bd8c","type":"libre","floor":"B1-UL1"},
      {"id":"Accueil Restaurant E2","color":"#1194ff","type":"reserve","floor":"B1-UL2"},
      {"id":"iViPlay","color":"#dd1185","type":"occupe","floor":"B1-UL3"}]
    localStorage.setItem('salles', JSON.stringify(salles));
    this.initializeForm();
    setInterval(() => {
      this.generalFunctions.refreshComponent(this.ngAfterViewInit());
    }, 1000);
  }

  goToMap(index: number) {
    console.log(index);
    localStorage.setItem('selectedroom', String(index));
    this.navigateController.navigateForward(['/tabs/map'])
  }

}
